import React from 'react'
import { Grid, Form, Button, Message } from 'semantic-ui-react'


class GradeForm extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      items: [],
      expectedGrade: 80,
    };
    this.addNewItem = this.addNewItem.bind(this);
    this.removeItem = this.removeItem.bind(this);
  }
  componentDidMount() {
    this.prefillItems();
  }
  prefillItems() {
    let items = [
      {
        "name": "A1",
        "mark": 80,
        "portion": 20,
      },
      {
        "name": "Midterm",
        "mark": 60,
        "portion": 30,
      },
      {
        "name": "Final",
        "mark": null,
        "portion": 50,
      }
    ];
    this.setState({ items: items });
  }
  addNewItem() {
    let items = this.state.items;
    items.push(
      {
        "name": null,
        "mark": null,
        "portion": null,
      }
    )
    this.setState({ items: items });
  }
  removeItem(index) {
    console.log(index);

    let items = this.state.items;
    items.splice(index, 1);
    this.setState({ items: items });
  }
  updateCell(index, type, value) {
    const mapping = { 0: "name", 1: "mark", 2: "portion" };
    let items = this.state.items;
    items[index][mapping[type]] = value
    this.setState({ items: items });
    console.log(items);

  }
  calculateGrade() {
    let items = this.state.items;
    // check for data correctness
    let emptyMarks = 0;
    let emptyPortion = 0;
    let totalPortion = 0;

    for (let i of items) {
      if (!i.mark) emptyMarks += 1;
      if (!i.portion) emptyPortion += 1;
      totalPortion += parseFloat(i.portion);
    }
    
    if (emptyMarks != 1 || emptyPortion != 0 || !this.state.expectedGrade || isNaN(this.state.expectedGrade) || totalPortion !== 100) return null;

    let currentTotal = 0;
    for (let i of items) {
      if (i.mark) {
        currentTotal += parseFloat(i.mark) * (parseFloat(i.portion) / 100)
      }
    }

    const neededMark = this.state.expectedGrade - currentTotal;
    if (neededMark < 0) return 0;

    for (let i of items) {
      if (!i.mark) {
        return neededMark / (parseFloat(i.portion) / 100)
      }
    }

    return 0;

  }
  renderResultMessage() {
    const needed = this.calculateGrade();
    
    if (needed == null) {
      return (
        <Message negative>
          <Message.Header></Message.Header>
          <Message.List>
            <Message.Item>%</Message.Item>
            <Message.Item>%</Message.Item>
            <Message.Item>%100</Message.Item>
          </Message.List>
        </Message>
      )
    }
    let neededName;
    for (let i of this.state.items){
      if (!i.mark) {
        neededName = i.name;
        break
      }
    }

    return (
      <Message>
        <Message.Header>{this.state.expectedGrade}</Message.Header>
        <p>
          {neededName}{needed.toFixed(1)}
          </p>
      </Message>
    )
  }
  render() {
    const itemLists = this.state.items.map((item, index) => 
      <Grid>
        <Grid.Row>
          <Grid.Column style={{ flex: 1 }}>
            <input onChange={(event) => this.updateCell(index, 0, event.target.value)} value={item.name || ''} placeholder={""} />
          </Grid.Column>
          <Grid.Column style={{ flex: 1 }}>
            <input onChange={(event) => this.updateCell(index, 1, event.target.value)} value={item.mark || ''} placeholder={"%"} />
          </Grid.Column>
          <Grid.Column style={{ flex: 1 }}>
            <input onChange={(event) => this.updateCell(index, 2, event.target.value)} value={item.portion || ''} placeholder={"%"} />
          </Grid.Column>
          <Grid.Column style={{ flex: 1 }}>
            <Button color='red' onClick={() => this.removeItem(index)}></Button>
          </Grid.Column>
        </Grid.Row>
      </Grid>
    );

    return (
      <Form>
        <Grid>
          <Grid.Row>
            <Grid.Column style={{ flex: 1 }}>
              
            </Grid.Column>
            <Grid.Column style={{ flex: 1 }}>
              %
            </Grid.Column>
            <Grid.Column style={{ flex: 1 }}>
              %
            </Grid.Column>
            <Grid.Column style={{ flex: 1 }}>
              
            </Grid.Column>
          </Grid.Row>
        </Grid>

        {itemLists}
        <div style={{ marginLeft: 1, marginTop: 16, marginBottom: 16 }}>
          <Button color='blue' attached='top' onClick={this.addNewItem}>
            
          </Button>
        </div>
        <Grid>
          <Grid.Row>
            <Grid.Column style={{ flex: 1, marginTop: 8 }}>
              
          </Grid.Column>
            <Grid.Column style={{ flex: 4 }}>
              <input value={this.state.expectedGrade} onChange={(event) => this.setState({ expectedGrade: event.target.value })} placeholder={""} />
            </Grid.Column>
          </Grid.Row>
        </Grid>

        <Grid>
          <Grid.Row>
            <Grid.Column>
              {this.renderResultMessage()}
            </Grid.Column>
          </Grid.Row>
        </Grid>
      </Form>


    );
  }
}

export default GradeForm;



